<template>
    <p-card style="width: 800px; margin: auto">
        <template #content>
            <div class="p-mb-3">
                <strong style="font-size: 1.5rem">Lab Mon</strong>
            </div>

            <p-data-table
                :value="labMonItems"
                responsiveLayout="scroll"
                showGridlines
                class="p-datatable-sm p-mb-1"
            >
                <p-column header="Name" field="name" />
                <p-column header="Active" field="active" />
                <p-column header="Category" field="category" />
                <p-column header="Ref Value" field="refValue" />
                <p-column header="Seq No" field="seqNo" />
                <p-column header="Disabled Fields" field="disabledFields" />
                <p-column header="Actions" style="width: 50px">
                    <template #body="{data}">
                        <div style="display: flex">
                            <p-button
                                icon="pi pi-pencil"
                                @click="editLabMonItem(data)"
                            />
                            <!-- <p-button
                                icon="pi pi-trash"
                                class="p-button-danger p-button-text"
                                @click="deleteLabMonItem(data)"
                            /> -->
                        </div>
                    </template>
                </p-column>
            </p-data-table>

            <div class="p-d-flex p-jc-between p-mt-2">
                <p-button label="Add" autofocus @click="showLabMonDialog" />
            </div>
        </template>
    </p-card>

    <p-dialog
        v-model:visible="addLabMonDialog"
        :modal="true"
        style="width: 600px"
    >
        <template #header>
            <div class="table-header">
                <strong>Add Lab Mon</strong>
            </div>
        </template>

        <div class="p-fluid">
            <div class="p-field">
                <label>Name</label>
                <p-input-text v-model="form.name" />
            </div>
            <div class="p-field">
                <label>Ref Value</label>
                <p-input-text v-model="form.refValue" />
            </div>
            <div class="p-field">
                <label>Seq No</label>
                <p-input-text v-model="form.seqNo" />
            </div>

            <div class="p-field">
                <label>Disabled Fields</label>
                <p-input-text v-model="form.disabledFields" />
            </div>
            <div class="p-field p-mt-2">
                <p-checkbox v-model="form.active" :binary="true" />
                <label class="p-ml-2 p-mb-0">Active</label>
            </div>
        </div>

        <div class="p-d-flex p-jc-between p-mt-2">
            <p-button
                label="Update"
                autofocus
                @click="updateLabMon"
                v-if="formIsEdit"
            />
            <p-button class="p-mt-4" label="Save" autofocus @click="addLabMon" v-else />
        </div>
    </p-dialog>
</template>

<script>
import { computed, inject, onMounted, reactive, ref } from "vue";
import { useStore } from "vuex";
import { $axios } from "@/service/app-http";
import { ORIGIN } from "../../config";

export default {
    setup() {
        const store = useStore();

        const addLabMonDialog = ref(false);
        const labMonItems = ref([]);

        const form = reactive({
            id: null,
            name: null,
            refValue: null,
            seqNo: null,
            disabledFields: null,
            active: true
        });

        const formIsEdit = ref(false);

        const getLabMonItems = async () => {
            try {
                const { data } = await $axios.get("lab-mon/list");
                labMonItems.value = data;
            } catch (e) {
                console.log(e);
            }
        };

        const addLabMon = async () => {
            try {
                form.active = true;
                const payload = { ...form };

                const { data } = await $axios.post("lab-mon/add", payload);

                labMonItems.value.push(data);

                addLabMonDialog.value = false;
                getLabMonItems();

            } catch (error) {
                console.error("Error adding Lab Mon:", error);
            }
        };

        const updateLabMon = async () => {
            try {
                const payload = { ...form };

                const { data } = await $axios.put(
                    `lab-mon/update/${payload.id}`,
                    payload
                );

                const index = labMonItems.value.findIndex(item => item.id === data.id);
                if (index !== -1) {
                    labMonItems.value[index] = data;
                }

                addLabMonDialog.value = false;
                getLabMonItems();

            } catch (error) {
                console.error("Error updating Lab Mon:", error);
            }
        };

        onMounted(() => {
            getLabMonItems();
        });

        return {
            form,
            formIsEdit,
            updateLabMon,
            addLabMon,
            addLabMonDialog,
            labMonItems,
            getLabMonItems,
            showLabMonDialog: () => {
                formIsEdit.value = false;

                form.id = null;
                form.name = null;
                form.refValue = null;
                form.seqNo = null;
                form.disabledFields = null;
                form.active = null;

                addLabMonDialog.value = true;
            },
            editLabMonItem: (data) => {
                formIsEdit.value = true;

                form.id = data.id;
                form.name = data.name;
                form.refValue = data.refValue;
                form.seqNo = data.seqNo;
                form.disabledFields = data.disabledFields;
                form.active = data.active;

                addLabMonDialog.value = true;
            }
        };
    }
};
</script>

<style scoped>
.table-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}
</style>
